@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  background-color: #EFEFEF;
  height: 100vh;

  display: flex;
  flex-direction: row;
}

.ingredients-container{

}

.recipe-container{
  
}

.ingredients-select{
  width: 30vw;
  height: 95vh;
  margin-left: 20px;
  margin-top: 20px;
  background-color: white;
  color: #FF3B3f;

  display: flex;
  flex-direction: column;
  align-self: center;
  /* align-items: center; */
  align-content: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.recipe-display{
  width: 65vw;
  height: 95vh;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
  background-color: #CAEBF2;
  color: #A9A9A9;

  display: flex;
  flex-direction: column;
  align-self: center;
  /* align-items: center; */
  align-content: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}
.LoginFlex{
    display: flex;
    flex-direction: row;
}

.RightHalf{
    margin-left: 25%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20%;
}

.LoginDiv{
    background-color: white;
    padding: 10% 5%;
}

.LogInEmail{
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    color: #333333;
}

.RegisterNow{
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    color: #333333;
    margin-top: 15%;
}

.InputClass{
    margin-top: 7%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}
